import React, { Component } from "react";
// import Option from "./Option";
import Question from "./Question";
import "./Questionare.css";

import NewReleasesIcon from "@material-ui/icons/NewReleases";

const allQuestions = [
  {
    question: (
      <>
        <div className="pt-5 pb-3">
          <NewReleasesIcon style={{ fontSize: "4rem", color: "#ff4343" }} />
        </div>
        <span>Is this an emergency?</span>
        <p>Stop and call 911 if you are experiencing:</p>
        <ul>
          <li key={0}>Severe, constant chest pain or pressure</li>
          <li key={1}>Extreme difficulty breathing</li>
          <li key={2}>Severe, constant lightheadedness</li>
          <li key={3}>Serious disorientation or unresponsiveness</li>
        </ul>
      </>
    ),
    options: [
      { text: "I am experiencing at least one of these symptoms.", score: 20 },
      { text: "I do not have any of these symptoms", score: 0 },
    ],
    type: "radio",
  },
  {
    question:
      "Are you experiencing any of these symptoms?(Select all that apply)",
    options: [
      { text: "a) fever (greater than 100.4 F or 38.0 C)", score: 4 },
      { text: "b) new or worsening cough", score: 2 },
      { text: "c) shortness of breath", score: 2 },
      { text: "d) difficulty breathing", score: 4 },
      { text: "e) sore throat", score: 2 },
      { text: "f) chills or sweating", score: 2 },
      { text: "g) Vomiting", score: 2 },
      { text: "h) diarrhea", score: 2 },
      { text: "i) None of the above", score: 0 },
    ],

    type: "checkbox",
  },
  {
    question: "Do any of these apply to you?(select all that apply)",
    options: [
      {
        text: "a)Moderate to severe asthma or chronic lung disease",
        score: 4,
      },
      {
        text: "b) Cancer treatment or medicines causing immune suppression",
        score: 4,
      },
      { text: "c) Inherited immune system deficiencies or HIV", score: 4 },
      {
        text:
          "d) Serious heart conditions, such as heart failure or prior heart attack",
        score: 4,
      },
      { text: "e) Diabetes with complications", score: 4 },
      { text: "f) Kidney failure that needs dialysis", score: 4 },
      { text: "g) Cirrhosis of the liver", score: 4 },
      {
        text: "h) Diseases or conditions that make it harder to cough",
        score: 4,
      },
      { text: "i) Extreme obesity", score: 4 },
      { text: "j) Pregnancy", score: 2 },
      { text: "k) None of the above", score: 0 },
    ],
    type: "checkbox",
  },
  {
    question:
      "In the last 14 days (On or after December), have you traveled internationally?",
    options: [
      { text: "a) I have traveled internationally", score: 10 },
      { text: "b) I have not traveled internationally", score: 0 },
    ],
    type: "radio",
  },
  {
    question:
      "In the last 14 days, have you been in an area where COVID19 is widespread?",
    options: [
      { text: "a) I live in an area where COVID-19 is widespread", score: 4 },
      {
        text: "b) I have visited an area where COVID-19 is widespread",
        score: 4,
      },
      { text: "c) I donâ€™t know", score: 2 },
      { text: "d) None of the above", score: 0 },
    ],
    type: "radio",
  },
  {
    question:
      "In the last 14 days, what is your exposure to others who are known to have COVID-19? (Select all that apply)",
    options: [
      { text: "a) I live with someone who has COVID-19", score: 4 },
      {
        text: "b) I've had close contact with someone who has COVID-19",
        score: 4,
      },
      {
        text:
          "c) I've been near someone who has COVID-19<br>(I was at least 6 feet away and was not exposed to a sneeze or cough.)",
        score: 2,
      },
      {
        text:
          "d) I've had no exposure<br>(I have not been in contact with someone who has COVID-19.)",
        score: 0,
      },
      { text: "e) I don't know", score: 2 },
    ],
    type: "radio",
  },
  {
    question:
      "Do you live in a care facility? This includes nursing homes or assisted living facilities.",
    options: [
      { text: "a) I live in a long-term care facility", score: 10 },
      { text: "b) No, I don't live in long-term care facility", score: 0 },
    ],
    type: "radio",
  },
  {
    question: (
      <>
        Do you work in a medical facility?
        <p>
          This includes a hospital, emergency room, other medical setting, or
          long-term care facility.
        </p>
        <p>Select all that apply.</p>
      </>
    ),
    options: [
      {
        text:
          "a) I have worked in a hospital or other care facility in the past 14 days (This includes volunteering.)",
        score: 6,
      },
      {
        text:
          "b) I plan to work in a hospital or other care facility in the next 14 days (This includes volunteering.)",
        score: 4,
      },
      {
        text: "c) No, I don't work or plan to work in a care facility",
        score: 0,
      },
    ],
    type: "radio",
  },
];

class Questionare extends Component {
  constructor(props) {
    super();
    this.state = {
      currQuesNo: 0,
      options: null,
      maxQuesNo: 0,
      completed: Array(allQuestions.length).fill(false),
      scores: Array(allQuestions.length).fill(0),
    };
  }
  componentDidMount() {
    console.log(this.state);
  }

  sendData = (totalScore) => {
    let probability = (totalScore / 120) * 100;
    fetch(`http://localhost:5000/stageonedata`, {
      method: "POST",
      mode: "cors",
      credentials: "include",
      headers: {
        "Content-Type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
      body: JSON.stringify({
        userId: 12345,
        location: "Vashi",
        probability: probability,
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        //Do something woth response
      })
      .catch((err) => {});
  };

  calcScore = () => {
    let totalScore = 0;
    this.state.scores.forEach((score) => {
      totalScore += score;
    });
    return totalScore;
  };

  nextQues = (event) => {
    if (this.state.currQuesNo < allQuestions.length - 1)
      this.setState({
        currQuesNo: this.state.currQuesNo + 1,
      });
    else if (this.state.currQuesNo === allQuestions.length - 1) {
      let allCompleted = true;
      this.state.completed.forEach((el) => {
        if (el === false) {
          allCompleted = false;
        }
      });
      console.log("allCompleted", allCompleted);
      if (allCompleted === false) {
        alert("Complete All Questions !!!");
      } else {
        this.sendData(this.calcScore());
      }
    }
  };

  prevQues = (event) => {
    if (this.state.currQuesNo > 0)
      this.setState({
        currQuesNo: this.state.currQuesNo - 1,
      });
  };

  onChange = (index, completedFlag, updatedScore) => {
    let newCompleted = this.state.completed;
    newCompleted[index] = completedFlag;
    let newScores = this.state.scores;
    newScores[index] = updatedScore;
    this.setState({
      completed: newCompleted,
      scores: newScores,
    });
    console.log(index, completedFlag, updatedScore);
  };

  render() {
    return (
      <div
        className="mt-3 p-3"
        style={{
          backgroundColor: "#f5f5f7",
          maxWidth: "800px",
          margin: "auto",
          border: "solid 1px #0071e3",
          borderRadius: "15px",
        }}
      >
        {allQuestions.map((object, index) => {
          return (
            <div
              key={index}
              style={{
                display: this.state.currQuesNo === index ? "block" : "none",
              }}
            >
              <Question
                key={index}
                index={index}
                onChange={this.onChange}
                question={object["question"]}
                options={object["options"]}
                type={object["type"]}
              />
            </div>
          );
        })}

        <div style={{ margin: "auto", display: "flex" }}>
          <span className="ques-btn p-2 pl-4 pr-4 m-3" onClick={this.prevQues}>
            Prev
          </span>
          <div style={{ flexGrow: 1 }}></div>
          <span className="ques-btn p-2 pl-4 pr-4 m-3" onClick={this.nextQues}>
            Next
          </span>
        </div>
      </div>
    );
  }
}
export default Questionare;
