import React, { Component } from "react";
import Option from "./Option";
// import "./Question.css";

class Question extends Component {
  constructor(props) {
    super();
    this.state = {
      currQues: null,
      options: null,
      type: null,
      selected: null,
    };
  }

  componentDidMount() {
    let selected = [];
    if (this.props.options !== null || this.props.options !== undefined)
      selected = Array(this.props.options.length).fill(false);
    this.setState({
      currQues: this.props.question,
      options: this.props.options,
      type: this.props.type,
      selected: selected,
    });
    console.log(this.state);
  }

  onOptCLick = (index) => {
    let newSelected;
    if (this.state.type === "radio") {
      newSelected = Array(this.state.selected.length).fill(false);
      newSelected[index] = true;
    } else {
      newSelected = this.state.selected;
      newSelected[index] = !newSelected[index];
    }
    this.setState({
      selected: newSelected,
    });
    console.log("selected", this.state.selected);
    let answeredFlag = false;
    let newScore = 0;
    newSelected.forEach((selBool, index) => {
      // console.log("Inside", index, option);

      if (selBool === true) {
        answeredFlag = true;
        newScore += this.state.options[index]["score"];
      }
    });
    console.log("Check", answeredFlag, newScore);

    this.props.onChange(this.props.index, answeredFlag, newScore);
  };

  render() {
    return (
      <div
        className="p-2"
        style={{
          maxWidth: "800px",
          margin: "auto",
          border: "solid 1px #e3e3e5",
          borderRadius: "15px",
          minHeight: "80vh",
        }}
      >
        <div
          className="pt-3 pb-2"
          style={{
            textAlign: "left",
            maxWidth: "500px",
            margin: "auto",
            fontSize: "1.2rem",
            fontWeight: 500,
          }}
        >
          {/* {this.props.index + 1})  */}
          {this.state.currQues}
        </div>
        <div>
          {this.state.options !== null && this.state.options !== undefined
            ? this.state.options.map((option, index) => {
                return (
                  <Option
                    key={index}
                    index={index}
                    optionText={option["text"]}
                    selected={this.state.selected[index]}
                    onOptClick={this.onOptCLick}
                  ></Option>
                );
              })
            : null}
        </div>
      </div>
    );
  }
}
export default Question;
